// –ü—Ä–æ–≤–µ—Ä–∫–∞ —è–∑—ã–∫–∞ –±—Ä–∞—É–∑–µ—Ä–∞
function checkBrowserLang() {
  const navLang = navigator.language.slice(0, 2).toLowerCase();
  const result = allLangs.some(elem => {
    return elem === navLang;
  });
  if (result) {
    return navLang;
  }
}

